import VueDragResize from 'vue-drag-resize'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('KnowledgeBase')
export default {
  name: 'DragResize',
  components: { VueDragResize },
  props: {
    maximized: {
      type: Boolean,
      required: true
    },
    coordinates: {
      type: Object,
      required: true
    },
    sticks: {
      type: Array,
      default: function() {
        return ['br', 'bl', 'tr', 'tl']
      }
    },
    stickStyle: {
      type: Object,
      default: function() {
        return {
          height: '15px',
          width: '15px',
          border: 'none',
          'box-shadow': 'none',
          'background-color': 'transparent'
        }
      }
    }
  },
  data() {
    return {
      active: false
    }
  },
  methods: {
    resize(newRect) {
      this.$emit('update:coordinates', {
        width: newRect.width,
        height: newRect.height,
        top: newRect.top,
        left: newRect.left
      })
    },
    onActivated() {
      const sticks = this.$refs.resize.$el.querySelectorAll('.vdr-stick')
      Array.prototype.map.call(sticks, element => {
        for (let prop in this.stickStyle) {
          element.style[prop] = this.stickStyle[prop]
        }
      })
    },
    onFocusElement(event) {
      event.target.focus()
    }
  },
  mounted() {
    this.active = true
  }
}
